import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { Account } from 'src/app/model/account';
import { Challan } from 'src/app/model/challan';
import { Transaction } from 'src/app/model/transaction';

@Component({
  selector: 'app-bill-receipt',
  templateUrl: './bill-receipt.component.html',
  styleUrls: ['./bill-receipt.component.scss'],
})
export class BillReceiptComponent implements OnInit {

  public account: Account;
  public transaction: Transaction;
  public challanRows: Challan[] = [];

  public totalAmount: number;
  
  constructor(private modalController: ModalController) { }

  ngOnInit() {
    this.challanRows = this.transaction.challanDetail ?? [];

    function getSum(total, num) {
      return total + num;
    }

    this.totalAmount = this.challanRows.map(challan => challan.weight * challan.rate).reduce(getSum, 0);
    
    const minNoRows = 8;
    if (this.challanRows.length < minNoRows) {
      for (let index = this.challanRows.length; index < minNoRows; index++) {
        this.challanRows.push(new Challan());
      }
    }
  }

  public onCloseClick() {
    this.modalController.dismiss();
  }

}
